
:root {
  --deepblack-bg: rgb(0, 0, 0);
  --newspaper-bg: rgb(199, 199, 199);
  --font-reg: 'Inter', sans-serif;
  --font-nun: 'Source Sans Pro', sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
  font-family: var(--font-reg);
}

body {
  background-color: #000;
  color: #fff;
  box-sizing: border-box;
  width: 100%;
  position: relative;
}

html {
  scroll-behavior: smooth;
}

header {
  position: sticky;
  top: 0;
  z-index: 1;
}

a {
  text-decoration: none;
  color: inherit;
}

.light {
  background-color: #f1f1f1;
  color: #000;
}

.dark {
  background-color: #000;
  color: #fff;
}

.logo {
  height: 80px;
  width: 80px;
  border-radius: 50%;
  border-color: #fff;
  border: 3px solid;
  margin: 10px;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;    
  align-items: center;
  padding-left: 30px;
  padding-right: 30px;
}

.header a {
  text-decoration: none;
}

.bulb-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.bulb {
  height: 40px;
  width: 40px;
  background-color: #000;
  border: 2px solid yellow;
  border-radius: 50%;
}

.bulb-bottom {
  height: 20px;
  width: 20px;
  margin-top: -4px;
  background-color: #f1f1f1;
}

.bead {
  height: 5px;
  width: 5px;
  background-color: gold;
  margin-top: 2px;
}

h1 {
  font-size: 80px;
}

.text-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-left: 10%;
  margin-right: 10%;
  height: 100vh;
  min-height: 1000px;
}

p {
  margin-top: 50px;
  font-size: 26px;
  font-family: var(--font-nun);
}

.text-section p {
  margin-top: 100px;
}

.directions {
  display: flex;
  flex-direction: column;
  position: absolute;
}

.top-row {
  display: flex;
}

.middle-row {
  display: flex;
}

.bottom-row {
  display: flex;
}

.stare {
  height: 33vh;
  width: 33vw;
  /* border: 1px solid yellow;
  background-color: rgb(0, 0, 0, 0.5); */
}

.marilyn {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr;
}

.box {
  display: flex;
  align-items: center;
  justify-content: center;
  height: calc(100vh / 2);
  width: 100%;
}

.head {
  display: flex;
  flex-direction: column;
  height: 80%;
  width: 80%;
  box-shadow: 1vw 2vw 0px 0px rgb(0, 0, 0, 0.7);
}

.hair {
  height: 30%;
  width: 100%;
}

.eyes {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  height: 20%;
  margin-top: 20px;
}

.eye {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 20%;
  border-radius: 50%;
  padding: 2%;
  
}

.eyeball {
  display: flex;
  height: 40%;
  width: 40%;
  border-radius: 50%;
  background-color: #000;
}

.mouth {
  align-self: center;
  margin-top: 10%;
  height: 5%;
  width: 30%;
  border-top: 1vw solid;
  border-right: 0.5vh solid;
  border-left: 0.5vw solid;
  border-bottom: 2vw solid;
  border-radius: 6px;
}

