/*-------INTERACTIVE ACCESSORIES--------*/


.project-component {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  min-height: 1000px;
  width: 100%;
}

.project-info {
  position: relative;
  text-align: center;
  padding-top: 30px;
  padding-bottom: 30px;
  margin-right: 10%;
  margin-left: 10%;
}

.project-component img {
  height: 400px;
  width: 100%;
  object-fit: contain;
  margin-top: 100px;
}

/*----------RESPONSIVE---------*/

@media only screen and (max-width: 1024px) {

  .logo {
    height: 50px;
    width: 50px;
  }

  .bulb {
    height: 30px;
    width: 30px;
  }
  
  .bulb-bottom {
    height: 15px;
    width: 15px;
    margin-top: -4px;
    background-color: #f1f1f1;
  }
  
  .bead {
    height: 3px;
    width: 3px;
    background-color: gold;
    margin-top: 2px;
  }

  .text-section {
    padding: 50px;
    margin: 50px auto;
  }

  .marilyn {
      grid-template-columns: 1fr 1fr;
  }

  .box {
      height: calc(100vw / 2);
  }

  h1 {
    font-size: 38px;
  }
}

@media only screen and (max-width: 700px) {
  .project-component img {
    margin-top: -50px;
  }
}